import Vue from 'vue'
import Router from 'vue-router'
// import index from '@/pages/pc/home'
// import importCar from '@/pages/pc/importCar'
// import secondHandCar from '@/pages/pc/secondHandCar'
// import leaseCar from '@/pages/pc/leaseCar'
// import importCarDesc from '@/pages/pc/importcardesc'
// import information from '@/pages/pc/information'
// import informationDesc from '@/pages/pc/information-desc'
// import leaseCarDesc from '@/pages/pc/leaseCardesc'
// import secondHandCarDesc from '@/pages/pc/secondHandcardesc'
// import stories from '@/pages/pc/stories'
// import questionList from '@/pages/pc/questionList'
// import aboutUs from '@/pages/pc/aboutus'
// import loan from '@/pages/pc/loan'
// import loanForm from '@/pages/pc/loanForm'

Vue.use(Router)


const pcIndex = (resolve) => {
  import('@/pages/pc/pc-index').then((module) => {
    resolve(module);
  })
}

const pcHome = (resolve) => {
  import('@/pages/pc/home').then((module) => {
    resolve(module);
  })
}
const importCar = (resolve) => {
  import('@/pages/pc/importCar').then((module) => {
    resolve(module);
  })
}
const secondHandCar = (resolve) => {
  import('@/pages/pc/secondHandCar').then((module) => {
    resolve(module);
  })
}
const leaseCar = (resolve) => {
  import('@/pages/pc/leaseCar').then((module) => {
    resolve(module);
  })
}
const importCarDesc = (resolve) => {
  import('@/pages/pc/importcardesc').then((module) => {
    resolve(module);
  })
}
const information = (resolve) => {
  import('@/pages/pc/information').then((module) => {
    resolve(module);
  })
}
const informationDesc = (resolve) => {
  import('@/pages/pc/information-desc').then((module) => {
    resolve(module);
  })
}
const leaseCarDesc = (resolve) => {
  import('@/pages/pc/leaseCardesc').then((module) => {
    resolve(module);
  })
}
const secondHandCarDesc = (resolve) => {
  import( '@/pages/pc/secondHandcardesc').then((module) => {
    resolve(module);
  })
}
const stories = (resolve) => {
  import('@/pages/pc/stories').then((module) => {
    resolve(module);
  })
}
const storyDesc = (resolve) => {
  import('@/pages/pc/storiesDesc').then((module) => {
    resolve(module);
  })
}
const questionList  = (resolve) => {
  import('@/pages/pc/questionList').then((module) => {
    resolve(module);
  })
}
const aboutUs = (resolve) => {
  import('@/pages/pc/aboutus').then((module) => {
    resolve(module);
  })
}
const loan = (resolve) => {
  import('@/pages/pc/loan').then((module) => {
    resolve(module);
  })
}
const loanForm = (resolve) => {
  import('@/pages/pc/loanForm').then((module) => {
    resolve(module);
  })
}

//手机端pages

const mobileIndex = (resolve) => {
  import('@/pages/mobile/m-index').then((module) => {
    resolve(module);
  })
}

const mobileSearchCar = (resolve) => {
  import('@/pages/mobile/m-searchCar').then((module) => {
    resolve(module);
  })
}

const mobileSearchCarDesc = (resolve) => {
  import('@/pages/mobile/m-searchCarDesc').then((module) => {
    resolve(module);
  })
}

const mobileSaleCar = (resolve) => {
  import('@/pages/mobile/m-saleCar').then((module) => {
    resolve(module);
  })
}

const mobileHome = (resolve) => {
  import('@/pages/mobile/m-home').then((module) => {
    resolve(module);
  })
}

const mobileConsultCar = (resolve) => {
  import('@/pages/mobile/m-consultCar').then((module) => {
    resolve(module);
  })
}

const mobileBrandList = (resolve) => {
  import('@/pages/mobile/m-brandList').then((module) => {
    resolve(module);
  })
}

const importCarList = (resolve) => {
  import('@/pages/mobile/m-importcarPage').then((module) => {
    resolve(module);
  })
}

const mobileimportCarDesc = (resolve) => {
  import('@/pages/mobile/m-importCarDesc').then((module) => {
    resolve(module);
  })
}

const secondHandCarList = (resolve) => {
  import('@/pages/mobile/m-secondHandcarPage').then((module) => {
    resolve(module);
  })
}

const mobileLoanList = (resolve) => {
  import('@/pages/mobile/m-loanList').then((module) => {
    resolve(module);
  })
}

const mobileLoanform= (resolve) => {
  import('@/pages/mobile/m-loanform').then((module) => {
    resolve(module);
  })
}

let pcRouter = new Router({
    routes: [
      {
        path: '/',
        component:pcIndex,
        name:"pcIndex",
        redirect: '/home'
      },
      {
        path: '/home',
        component:pcHome,
        name:"pcHome"
      },
      {
        path:'/importCar',
        component:importCar,
        name:'importCar'
      },
      {
        path:'/leaseCar',
        component:leaseCar,
        name:'leaseCar'
      },
      {
        path:'/secondHandCar',
        component:secondHandCar,
        name:'secondHandCar'
      },
      {
        path:'/importCarDesc/:id',
        component:importCarDesc,
        name:'importCarDesc'
      },
      {
        path:'/leaseCarDesc/:id',
        component:leaseCarDesc,
        name:'leaseCarDesc'
      },
      {
        path:'/secondHandCarDesc/:id',
        component:secondHandCarDesc,
        name:'secondHandCarDesc'
      },
      {
        path:'/information',
        component:information,
        name:'information'
      },
      {
        path:'/information/:id',
        component:informationDesc,
        name:'informationDesc'
      },
      {
        path:'/stories',
        component:stories,
        name:'stories'
      },
      {
        path:'/stories/:id',
        component:storyDesc,
        name:'storyDesc'
      },
      {
        path:'/questionList',
        component:questionList,
        name:'questionList'
      },
      {
        path:'/aboutUs',
        component:aboutUs,
        name:'aboutUs'
      },
      {
        path:'/loan',
        component:loan,
        name:'loan'
      },
      {
        path:'/loanForm/:id',
        component:loanForm,
        name:'loanForm'
      }
    ]
})

let mobileRouter = new Router({
  routes: [
    {
      path: '/',
      component:mobileIndex,
      name:"mobileIndex",
      redirect: '/home'
    },
    {
      path: '/home',
      component:mobileHome,
      name:"mobileHome",
      children:[
        {
          path:'brandList',
          component:mobileBrandList,
          name:'mobileBrandList'
        },
        {
          path:'importcarList',
          component:importCarList,
          name:'importCarList',
          children:[
            {
              path:'brandList',
              component:mobileBrandList,
              name:'mobileBrandList'
            },
            {
              path:'importCarDesc',
              component:mobileimportCarDesc,
              name:'mobileimportCarDesc'
            }
          ]
        },
        {
          path:'secondHandcarList',
          component:secondHandCarList,
          name:'secondHandCarList',
          children:[
            {
              path:'brandList',
              component:mobileBrandList,
              name:'mobileBrandList'
            }
          ]
        },
        {
          path:'loanList',
          component:mobileLoanList ,
          name:'mobileLoanList',
          children:[
            {
              path:'loanform',
              component:mobileLoanform,
              name:'mobileLoanform'
            }
          ]
        }
      ]
    },
    {
      path: '/mobileSearchCar',
      component:mobileSearchCar,
      name:"mobileSearchCar",
      children:[
        {
          path:'brandList',
          component:mobileBrandList,
          name:'mobileBrandList'
        },
        {
          path:"mobileSearchCarDesc",
          component:mobileSearchCarDesc,
          name:'mobileSearchCarDesc'
        }
      ]
    },
    {
      path: '/mobileSaleCar',
      component:mobileSaleCar,
      name:"mobileSaleCar"
    },
    {
      path: '/mobileConsultCar',
      component:mobileConsultCar,
      name:"mobileConsultCar"
    },
  ]
})



let deviceWidth = document.body.clientWidth;

let router;

if(deviceWidth < 1200){
  router = mobileRouter;
 }else{
  router = pcRouter;
 }

export default router;


